export default {
  name: "DropDownList",
  data() {
    return {
      activeIndex: -1
    };
  },
  props: {
    dataList: {
      // 遍历数据
      type: [Array, Object],
      default() {
        return [{ name: "选项一" }, { name: "选项二" }];
      }
    },

    labelProperty: {
      // prop键名
      type: String,
      default() {
        return "name";
      }
    },

    placeholder: {
      // 默认提示
      type: String,
      default() {
        return "请输入";
      }
    },
    align: {
      // 对齐方式
      type: String,
      default: () => "center"
    },

    size: {
      // 显示个数
      type: [Number, String],
      default: () => 4
    },

    disabled: {
      type: Boolean,
      default: () => false
    }
  },
  directives: {
    dpl: {
      bind(el) {
        el.style.display = "none";
      }
    }
  },
  methods: {
    onDplOver(event) {
      // 禁用状态不允许操作
      if (this.disabled) return;
      console.dir(event.currentTarget.childNodes);
      let ul = event.currentTarget.childNodes[1];

      ul.style.display = "block";
    },
    onDplOut(event) {
      // 禁用状态不允许操作
      if (this.disabled) return;
      let ul = event.currentTarget.childNodes[1];
      ul.style.display = "none";
    },
    onLiClick(index) {
      // 禁用状态不允许操作
      if (this.disabled) return;
      let path = event.path || (event.composedPath && event.composedPath()); //兼容火狐和safari
      path[1].style.display = "none";
      this.activeIndex = index;
      this.$emit("change", {
        index: index,
        value: this.dataList[index]
      });
    }
  },
  computed: {
    dplLable() {
      return this.dataList[this.activeIndex]
        ? this.dataList[this.activeIndex][this.labelProperty]
        : this.placeholder;
    }
  },
  watch: {
    dataList() {
      // 当遍历数据发生更替时, 重置下拉菜单
      this.activeIndex = -1;
    }
  }
};
