/**
 * 注: 此页面只为移动端服务, 在pc模式下会导致页面失去比例
 * 招商h5页
 */

import { commitAgent } from "~/api/agent";
import { getCity } from "~/api/base";
import cityList from "~/utils/city_code";
import DropDownList from "~/components/mg_select";
export default {
  head: {
    script: [
      {
        src:
          "https://webapi.amap.com/maps?v=1.4.5&key=1e83fc87d44ee15162d8b207e44dd4fb"
      }
    ]
  },
  components: {
    DropDownList
  },
  data() {
    return {
      username: "", // 用户姓名
      userTel: "", // 联系电话
      firmName: "", // 公司名称
      siteDetail: "", // 详细地址
      province_code: "", // 省 code
      city_code: "", // --- 市 code
      district_code: "", // 县/区 code

      provincesList: [], // 省列表
      cityList: [], // --- 市 列表
      countyList: [] // -- 县/区列表
    };
  },

  mounted() {
    // 获取所有的省份
    if (process.client) {
      document.title = "【蜂乐购优选】区域会员制社交电商平台";
    }

    this.getCity().then(res => {
      if (res.code === 1000) {
        this.provincesList = res.data;
      } else {
        alert("网络出现故障!");
      }
    });
    if (process.client) {
      let isFristMap;
      let key = setTimeout(() => {
        this.getMap();
        isFristMap = true;
      }, 1000);
      window.onload = () => {
        if (isFristMap) return;
        clearTimeout(key);
        this.getMap();
      };
    }
  },
  methods: {
    commit() {
      if (!this.username) {
        alert("姓名不能为空");
        return;
      }
      if (!this.userTel) {
        alert("手机号码不能为空");
        return;
      }

      if (!this.province_code || !this.city_code || !this.district_code) {
        alert("请填写完整代理城市");
        return;
      }

      commitAgent({
        user_name: this.username, // 姓名
        user_tel: this.userTel, // 联系电话
        province_code: this.province_code, // 省 code
        city_code: this.city_code, // -- 市 code
        district_code: this.district_code, // 县/区 code
        address_detail: this.siteDetail, // 详细地址
        business_name: this.firmName, // 公司名称
        code_type: 14, // 固定值
        from: "h5" // h5版本的招商提交页
      }).then(res => {
        if (res.data.code === 1000) {
          alert("提交成功,1-3个工作日联系您！");
        } else {
          alert(res.data.message);
        }
      });
    },

    getCity(code = "") {
      return getCity({ code }).then(res => res.data);
    },
    onProvincesChange(val) {
      let code = val.value.code;
      // 改变省的时候, 重置市的code码以及队列
      if (this.province_code != code) {
        this.city_code = "";
        this.district_code = "";
        this.cityList = [];
        this.countyList = [];
      }
      this.province_code = code;
      this.getCity(code).then(res => {
        if (res.code === 1000) {
          this.cityList = res.data;
        } else {
          alert("网络出现故障!");
        }
      });
    },
    onCityChange(val) {
      let code = val.value.code;
      // 改变市的时候, 重置县/区的code码
      if (this.city_code != code) {
        this.district_code = "";
        this.countyList = [];
      }
      this.city_code = code;

      this.getCity(code).then(res => {
        if (res.code === 1000) {
          this.countyList = res.data;
        } else {
          alert("网络出现故障!");
        }
      });
    },
    onCountyChange(val) {
      let code = val.value.code;
      this.district_code = code;
    },

    getMap() {
      // AMap是高德地图的构造函数，这里.Map是创建地图，.Marker是创建坐标点   28.2076936875,112.8821611404
      let mapObj = new AMap.Map("mapContainer", {
        //'container'是对应页面盒子的id
        resizeEnable: true, //自适应大小
        zoom: 17, //初始视窗
        center: [112.8821611404, 28.2076936875] //中心点
      });
      new AMap.Marker({
        map: mapObj,
        position: new AMap.LngLat("112.8821611404", "28.2076936875") //此处根据页面数据可以直接传入经纬度进行描点
      });
    }
  }
};
